.header{
	background:url(../images/bg.jpg) no-repeat;
	background-size: cover;
	background-position: center;
	height: 950px;
	position:relative;
}
.logo{
	padding-top:55px;
}
.header__title{
	color:white;
	font-size:60px;
	font-weight: lighter;
	margin-top:330px;
}
.header__desc{
	color: white;
	font-size: 13px;
	display:block;
	margin-top:10px;
	line-height:24px;
}
.header__iphone{
	background:url(../images/iPhone.png) no-repeat;
	width:650px;
	height:900px;
	position:absolute;
	right: 15px;
    top: 260px;
}
.header .perfect__site {
    position: absolute;
    transform: rotate(-90deg);
    top: 535px;
    text-transform: uppercase;
    color: white;
    font-weight: lighter;
}

.header .perfect__site span {
    font-weight: bold;
    padding-left: 20px;
}

