.description{
    position:relative;
}
.description__title{
	font-size: 35px;
	line-height: 24px;
	font-weight: lighter;
}
.description__about{
	padding-top: 115px;
}
.col-md-6 p{
    margin-top: 30px;
    font-size: 14px;
    color: #959595;
    line-height: 23px;
}
.col-md-6 p:first-child{
	margin-top:34px;
}
.col-md-6.collumn{
	margin-left: 22px;
    position: relative;
    top: -47px;
}
.description__gallery>div {
    display: inline-block;
    height: 280px;
    vertical-align: top;
    padding: 50px 40px;
    position: relative;
    opacity:0.9;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent;
    &:hover, &:hover::before{
    	opacity:1;
    }
    &::before{
    	content:" ";
    	position: absolute;
	    border: white solid 4px;
	    top: 4px;
	    left: 4px;
	    right: 4px;
	    bottom: 4px;
	    opacity: 0;
	    -webkit-transition-duration: 0.3s;
	    transition-duration: 0.3s;
	    -webkit-transition-property: opacity;
	    transition-property: opacity;
    }
}
.description__design {
    background-color: #f5989d;
}

.description__build {
    background-color: #615f7d;
}

.description__clients {
    background-color: #091e28;
}

.description__perfect {
    background-color: #82ca9c;
}
.description__design, .description__build, .description__clients {
    width: 385px;
}
.description__sign {
    width: 770px;
    background:url(../images/sign.jpg) no-repeat;
    background-size:cover;
}

.description__perfect {
    width: 385px;
}
.description__gallery{
	margin-top: 77px;
	font-size:0;
    margin-bottom: 100px;
}
.gallery__title{
	font-size: 30px;
	font-weight: lighter;
	color: white;
}
.gallery__description{
	font-size: 13px;
	color: white;
	width: 310px;
}
.description__sign .gallery__description{
	width: 450px;
}
.gallery__more{
	display: block;
	position: absolute;
    bottom: 60px;
    color: white;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
    &:hover i{
    	-webkit-transform: translateX(4px);
    	transform: translateX(4px);
    	display: inline-block;
    }
}
.description__arow{
	font-size: 16px;
    position: relative;
    margin-left: 6px;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-transition-duration: 0.1s;
    transition-duration: 0.1s;
    -webkit-transition-property: transform;
    transition-property: transform;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}
.description__heart{
    background-image: url(../images/fa-heart-o.png);
    width: 18px;
    height: 17px;
    position: absolute;
    right: 30px;
    bottom:  60px;
    cursor: pointer;
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px transparent;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    &:hover{
        -webkit-transform: scale(1.3) translateZ(0);
        transform: scale(1.3) translateZ(0);
    }
}
.description__row {
    margin-top: 65px;
    margin-bottom: 100px;
}
.description__detail {
    display: inline-block;
    width: 260px;
    margin-right: 34px;
}
.description__detail:last-child {
    margin: 0;
}
.description__titl {
    font-weight: 600;
    font-size: 13px;
    margin-bottom: 19px;
    color: #67676d;
}
.description__text {
    color: #959595;
    font-size: 13px;
    line-height: 24px;
}
.description .perfect__site {
    position: absolute;
    transform: rotate(-90deg);
    bottom: 155px;
    text-transform: uppercase;
    color: #959595;
    font-weight: lighter;
}

.description  .perfect__site span {
    font-weight: bold;
    padding-left: 20px;
}


@media(max-width:1386px){
    .perfect__site {
    left: -6%;
    }
}
@media(max-width:1250px){
    .perfect__site {
        display: none;
    }   
}
@media(max-width:1180px){
    .row{
        width:960px;
    }
    .description__sign{
        width: 639px;
    }
    .description__build, .description__clients, .description__design, .description__perfect{
        width: 319px;
    }
    .gallery__description{
        width: 250px;
    }
    .col-md-6.collumn{
        top: -135px;
    }
    .col-md-6 {
        width: 455px;
    }
    .description__detail{
        width: 210px;
    }
}
@media (max-width: 960px){
    .row{
        width: auto;
    }
    .header__info {
    position: relative;
    z-index: 9;
    background: rgba(0,0,0,0.4);
    text-align: center;
    }
.logo{
    padding-left: 30px;
}
    .header__iphone {
        display: none;
    }
    .description__about {
    padding-top: 50px;
}
.col-md-6 {
    width: auto;
    display: block;
}
.col-md-6.collumn {
    top: 0;
    margin-left:0;
}
.description__text{
    padding:0 30px;
}
.description__gallery{
    text-align: center;
}
.description__build, .description__clients, .description__design, .description__perfect{
    width:299px;
}
.description__sign{
    width: 600px;
}
.description__row{
    text-align: center;
}
.description__detail{
    margin-bottom: 30px;
}
.gallery__more{
    left: 55px;
}
}
@media (max-width: 900px){
    .description__gallery{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
    .description__sign {
        order: 5;
        display: flex !important;
}
    .description__sign{
        width: 598px;
    }
}
@media (max-width:600px){
    body{
        min-width:320px;
    }
    .description__build, .description__clients, .description__design, .description__perfect{
        width: auto;
    }
    .gallery__description {
        width: auto;
    }
    .description__sign{
        width: auto;
    }
    .description__sign .gallery__description {
        width: auto;
        text-align: right;
    }
    .description__heart, .gallery__more{
        bottom: 20px;
    }
}